	}

	.navbar-trigger span:nth-child(2) {
		top: 50%;
		margin-top: -1px;
	}

	.navbar-trigger span:nth-child(3) {
		bottom: 8px;
	}

	.navbar-trigger.active span:nth-child(1) {
		top: 50%;
		margin-top: -1px;
		transform: rotate(45deg);
	}

	.navbar-trigger.active span:nth-child(2) {
		opacity: 0;
	}

	.navbar-trigger.active span:nth-child(3) {
		bottom: 50%;
