@media (max-width: 767px) {

	.timer-arrow,
	.webinar-tmb,
	.webinars-arrow,
	.mark-tmb,
	.about-num,
	.about-arrow,
	.timer-wrapper *[data-index],
	.prize-line,
	.stages-arrow,
	.task-wrapper *[data-anim-index],
	.prizes-col,
	.stage-tmb,
	.part-arrow,
	.part-col,
	.task-top-col,
	.top-arrow,
	.section-top-pics .anim,
	.section-top-descr .anim,
	.section-top-header .anim {
		transform: none !important;
		opacity: 1 !important;
	}

}

.section-top-header .anim,
.section-top-descr .anim {
	opacity: 0;
	transform: translate(0, 50px);
}

.section-top-pic-1 {
	opacity: 0;
	transform: translate(30px, -30px);
}

.section-top-pic-2 {
	opacity: 0;
	transform: translate(-30px, 30px);
}

.top-arrow-1,
.top-arrow-2 {
	opacity: 0;
}

.task-wrapper *[data-anim-index] {
	opacity: 0;
}

.about-arrow {
	opacity: 0;
	transform: translate(0, -50px);
}

.mark-tmb {
	opacity: 0;
	transform: translate(0, 50px);
}

.about-num {
	opacity: 0;
	transform: translate(0, 50px);
}

.part-col {
	opacity: 0;
	transform: translate(0, 50px);
}

.part-arrow {
	opacity: 0;
}

.stage-tmb {
	opacity: 0;
	transform: translate(-50px, 0);
}

.prizes-col {
	opacity: 0;
	transform: translate(0, 50px);
}

.from-left {
	transform: translate(-50px, 0);
}

.from-top {
	transform: translate(0, -50px);
}

.from-right {
	transform: translate(50px, 0);
}

.from-bottom {
	transform: translate(0, 50px);
}

.stages-arrow {
	opacity: 0;
	transform: translate(0, 50px);
}

.prize-line {
	opacity: 0;
	transform: translate(0, 50px);
}

.timer-wrapper *[data-index] {
	opacity: 0;
}

.webinars-arrow-1 {
	opacity: 0;
	transform: translate(-50px, 0);
}

.webinars-arrow-2 {
	opacity: 0;
	transform: translate(50px, 0);
}

.col:nth-child(1) .webinar-tmb {
	opacity: 0;
	transform: translate(-50px, 0);
}

.col:nth-child(2) .webinar-tmb {
	opacity: 0;
	transform: translate(50px, 0);
}

.timer-arrow-1 {
	transform: translate(-50px, 0);
	opacity: 0;
}

.timer-arrow-2 {
	transform: translate(50px, 0);
	opacity: 0;
}